@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600&family=Zen+Loop:ital@0;1&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background-color: #22254b;
  color: white;
  font-family: Montserrat;
}

.App{
  display: flex;
  flex-direction: column;
}

.search-container{
  background-color: #373b69;
  display: flex;
  justify-content: flex-end;
  padding: 15px;
}

.search{
  background-color: white;
  border: 2px solid #22254b;
  border-radius: 30px;
  font-family: inherit;
  font-size: 15px;
  padding: 7px 21px;
  color: #22254b;
}

.search:focus{
  outline: none;
}

@media screen and (max-width: 500px) {

  .search-container{
    padding: 10px;
  }
  
  .search{
    font-size: 7px;
    padding: 7px 21px;
  }

  body{
    font-weight: 300;
  }

}

@media screen and (max-width: 400px) {
  body{
    font-weight: 200;
  }
}
.movies{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.movie-container{
    background-color: #373b69;
    border-radius: 5px;
    box-shadow: 3px 3px 5px rgba(0,0,0,0.1);
    overflow: hidden;
    width: 250px;
    margin: 20px;
    position: relative;
}

.movie-container img{
    max-width: 100%;
}

.movie-info{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
}

.movie-info p{
    margin: 0;
}

.movie-overview{
    background-color: white;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    color: #22254b;
    padding: 10px 20px 20px;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    transition: -webkit-transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
    overflow: auto;
    max-height: 100%;
    border-radius: 5px;
    box-shadow: 0px -3px 5px rgba(0,0,0,0.1);
}

.movie-overview h2{
    padding: 7px 0;
}

.movie-overview p{
    font-family: Zen loop;
    font-size: large;
    font-weight: 700;
}

.movie-container:hover .movie-overview{
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
}

.tag{
    background-color: #22254b;
    padding: 7px;
    border-radius: 5px;
    font-weight: bold;
}

.tag.green{
    color: rgb(11, 179, 11);
}

.tag.orange{
    color: orange;
}

.tag.red{
    color: red;
}

@media screen and (max-width: 900px) {
    .movie-container{
        width: 250px;
        margin: 20px 10px;
    }
}


@media screen and (max-width: 700px) {
    .movie-container{
        width: 200px;
        margin: 20px 12px;
    }
}


@media screen and (max-width: 460px) {
    .movie-container{
        width: 170px;
        margin: 20px 7px;
    }
}

@media screen and (max-width: 400px) {
    .movie-container{
        width: 150px;
        margin: 20px 7px;
    }

    .movie-info{
        padding: 5px 7px;
    }
}
